import React from 'react';
// Se utiliza bootstrap, requiere su instalacion por el medio que estime conveniente

// Componente requiere ingresar el color en formato rga tipo:  rgbaColor = "241, 196, 15" como propiedad en el componente
// Requiere valor porcentual de la barra tipo: now="90" como propiedad en el componente
// Se recomiendan colores de https://htmlcolorcodes.com/es/tabla-de-colores/


export function ProgressBar(props){
    let color = props.rgbaColor;
    let color1 = "rgba(" + color + ", 0.2)"; 
    let color2 = "rgba(" + color + ", 0.8)";
    let progressvalue = props.now;
    return(
        <div className="progress " style={{backgroundColor: color1, width:"100%", height:"4px"}}>
            <div className="progress-bar" role="progressbar" style={{width:progressvalue+"%", backgroundColor: color2}} aria-valuenow={progressvalue} aria-valuemin="0" aria-valuemax="100"></div>
        </div>
    );
}
export default ProgressBar;